.page_blog {
	.sect_nav {
		padding: rem(24) 0 rem(8) 0;
		ul {

			li {
				width: calc((100% - 2rem) / 5);
				margin-right: rem(18);
				display: flex;
				justify-content: center;

				&:nth-child(4n-7) {
					// display: flex;
					justify-content: flex-start;
				}

				&:nth-child(4n) {
					margin-right: 0;
					// display: flex;
					justify-content: flex-end;
				}
			}
		}
	}

	.w-full {
		border-top: rem(1) solid #E5E8EC;
		padding-top: rem(60);

		
	}

	.articles {
		padding: rem(72) 0;
	}
}

.default_subscribe {
	.subscribe__form {
		padding: 0 rem(84);
		margin-bottom: rem(72);

		.title {
			font-size: rem(24);
			letter-spacing: rem(1.2);
			text-align: center;
			margin-bottom: rem(36);
		}

		.field {
			height: rem(56);
		}
	}

	.form_send_success {

		.form_success {
			padding: 0 rem(84);
			top: rem(72);
		}
	}
}